            </div>
            <div id="timerSetting" class="collapse bg-light pb-3 pt-3 mb-2">
                <div class="container">
                    <div class="row">
                        <div class="col-12 mb-3">
                            <div class="btn-group btn-group-toggle">
                                <label class="btn btn-outline-secondary" :class="{active: timerType === 'timer'}">
                                    <input type="radio" name="timerType" autocomplete="off" value="timer"
                                           v-model="timerType">{{ $t('text.timerMode') }}
