                                    <input type="input" class="form-control" id="choose-bg-color"
                                           v-model="style.bgColor">
                                </div>
                                <div class="col-2 pl-0">
                                    <input type="color" class="form-control" v-model="style.bgColor">
                                </div>
                            </div>
                        </div>
                        <div class="col-12 col-md-4 mt-1">
                            <label for="choose-font-color">{{ $t('text.fontColor') }}</label>
                            <div class="input-group-sm row">
                                <div class="col-8 pr-0">
                                    <input type="input" class="form-control" id="choose-font-color"
                                           v-model="style.fontColor">
                                </div>
                                <div class="col-2 pl-0">
                                    <input type="color" class="form-control" v-model="style.fontColor">
                                </div>
                            </div>
                        </div>
                        <div class="col-12 col-md-6 mt-1">
                            <label>{{ $t('text.charStyle') }}</label>
                            <div class="input-group">
                                <div class="btn-group btn-group-toggle"
