                                    </label>
                                    <label class="btn btn-outline-info" :class="{active: style.charId === '//'}">
                                        <input type="radio" name="char" autocomplete="off" value="//"
                                               v-model="style.charId">&nbsp;//&nbsp;&nbsp;//&nbsp;
                                    </label>
                                    <label class="btn btn-outline-info" :class="{active: style.charId === '\\/'}">
                                        <input type="radio" name="char" autocomplete="off" value="\/"
                                               v-model="style.charId">&nbsp;\&nbsp;&nbsp;/&nbsp;
                                    </label>
                                </div>
                            </div>
                        </div>
                        <div class="col-12 col-md-6 mt-1">
                            <label>{{ $t('text.theme') }}</label>
                            <div class="input-group" style="overflow-x: auto">
                                <div v-for="(item, key) in supportStyleList">
                                    <button type="button" class="btn btn-outline-dark mr-1"
                                            :style="{color: item.color, backgroundColor: item.bgColor}"
                                            @click="setColor(key)">
                                        {{ getChar[0] }}&nbsp;{{ getChar[1] }}
                                    </button>
                                </div>
                            </div>
                        </div>
                        <div class="col-12 col-md-5 mt-1">
