                        </div>
                        <div class="form-group col-12 col-md-3 mt-1" v-if="timerType === 'countDown'">
                            <label for="dayInput">{{ $t('text.day') }}</label>
                            <select id="dayInput" class="form-control" v-model="countDown.day"
