                        </div>
                        <div class="form-group col-12 col-md-3 mt-1" v-if="timerType === 'countDown'">
                            <label for="hourInput">{{ $t('text.hour') }}</label>
                            <select id="hourInput" class="form-control" v-model="countDown.hour"
                                    @change="renderInitTime">
                                <option v-for="i in 24" :value="i-1">{{ i-1 }}</option>
