                        </div>
                        <div class="form-group col-12 col-md-3 mt-1" v-if="timerType === 'countDown'">
                            <label for="minInput">{{ $t('text.min') }}</label>
                            <select id="minInput" class="form-control" v-model="countDown.min" @change="renderInitTime">
                                <option v-for="i in 60" :value="i-1">{{ i-1 }}</option>

                            </select>
                        </div>
                        <div class="form-group col-12 col-md-3 mt-1" v-if="timerType === 'countDown'">
                            <label for="secInput">{{ $t('text.sec') }}</label>
                            <select id="secInput" class="form-control" v-model="countDown.sec" @change="renderInitTime">
                                <option v-for="i in 60" :value="i-1">{{ i-1 }}</option>
                            </select>
                        </div>
                        <div class="col-12 col-md-6 mt-1">
                            <label>顯示控制</label>
                            <div>
                                <div class="form-check form-check-inline">
                                    <input class="form-check-input" type="checkbox" value="true" id="displayDay" v-model="display.day">
                                    <label class="form-check-label" for="displayDay">
                                        {{ $t('text.day') }}
                                    </label>
                                </div>
                                <div class="form-check form-check-inline">
                                    <input class="form-check-input" type="checkbox" value="true" id="displayHour" v-model="display.hour">
                                    <label class="form-check-label" for="displayHour">
                                        {{ $t('text.hour') }}
                                    </label>
                                </div>
                                <div class="form-check form-check-inline">
                                    <input class="form-check-input" type="checkbox" value="true" id="displayMin" v-model="display.min">
                                    <label class="form-check-label" for="displayMin">
                                        {{ $t('text.min') }}
                                    </label>
                                </div>
                                <div class="form-check form-check-inline">
                                    <input class="form-check-input" type="checkbox" value="true" id="displaySec" v-model="display.sec">
                                    <label class="form-check-label" for="displaySec">
                                        {{ $t('text.sec') }}
                                    </label>
                                </div>
                                <div class="form-check form-check-inline">
                                    <input class="form-check-input" type="checkbox" value="true" id="displaymicrosec" v-model="display.microSec">
                                    <label class="form-check-label" for="displaymicrosec">
                                        {{ $t('text.microSec') }}
                                    </label>
                                </div>
                            </div>
                        </div>
                        <div class="col-12 col-md-6 mt-1">
                            <label>{{ $t('text.separatorSymbol') }}</label>
                            <div class="input-group">
                                <div class="btn-group btn-group-toggle"
                                     style="overflow-x: auto">
                                    <label v-for="(item, key) in separatorSymbol" class="btn btn-outline-success" :class="{active: style.separatorSymbolId === key}">
                                        <input type="radio" name="char" autocomplete="off" :value="key"
                                               v-model="style.separatorSymbolId" checked>{{ item.hour }}&nbsp;{{ item.min }}&nbsp;{{ item.sec }}
                                    </label>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
        <div id="timerBody" class="d-flex justify-content-center align-items-center"
             :style="{backgroundColor: style.bgColor, color: style.fontColor}">
            <div class="flex-column align-items-center">
                <div class="display-1 d-flex align-items-stretch">
                    <div class="align-self-center" :style="{fontSize: style.fontSize * 2.2 + 'vw'}"
                         style="font-weight: lighter">
                        {{ getChar[0] }}&nbsp;
                    </div>
                    <div class="align-self-center flex-column">
                        <div class="text-center" :style="{fontSize: style.fontSize * 0.3 + 'vw'}">
                            {{style.title}}
                        </div>
                        <div class="text-center text-monospace">
                            <span v-if="display.day && timerType !=='now'" :style="{fontSize: style.fontSize + 'vw'}">{{ time.day }}</span>
                            <span v-if="display.day && (display.hour || display.min || display.sec) && timerType !=='now'" style="opacity: 0.5" :style="{fontSize: style.fontSize * 0.5 + 'vw'}">{{ separatorSymbol[style.separatorSymbolId].day }}</span>
                            <span v-if="display.hour" :style="{fontSize: style.fontSize + 'vw'}">{{ getHour }}</span>
                            <span v-if="display.hour && (display.min || display.sec)" style="opacity: 0.5" :style="{fontSize: style.fontSize * 0.5 + 'vw'}">{{ separatorSymbol[style.separatorSymbolId].hour }}</span>
                            <span v-if="display.min" :style="{fontSize: style.fontSize + 'vw'}">{{ getMin }}</span>
                            <span v-if="display.min && display.sec" style="opacity: 0.5" :style="{fontSize: style.fontSize * 0.5 + 'vw'}">{{ separatorSymbol[style.separatorSymbolId].min }}</span>
                            <span v-if="display.sec" :style="{fontSize: style.fontSize + 'vw'}">{{ getSec }}</span>
                            <span v-if="display.min && display.sec" style="opacity: 0.5" :style="{fontSize: style.fontSize * 0.5 + 'vw'}">{{ separatorSymbol[style.separatorSymbolId].sec }}</span>
                            <span v-if="display.microSec" style="opacity: 0.5" :style="{fontSize: style.fontSize * 0.5 + 'vw'}">{{ separatorSymbol[style.separatorSymbolId].microSec }}{{ getMicroSec }}</span>
                        </div>
                        <div class="text-center" :style="{fontSize: style.fontSize * 0.15 + 'vw'}"
                             style="opacity:0.5">
                            {{style.subTitle}}
                        </div>
                        <div class="text-center mt-3" :style="{fontSize: style.fontSize * 0.15 + 'vw'}">
                            <font-awesome-icon icon="play" v-if="this.isPause" class="btn-icon fa-1x" @click="play"/>
                            <font-awesome-icon icon="pause" v-if="!this.isPause" class="btn-icon fa-1x" @click="pause"/>
                            &nbsp;&nbsp;
                            <font-awesome-icon icon="flag" class="btn-icon fa-1x" @click="fetch"/>
                            &nbsp;&nbsp;
                            <font-awesome-icon icon="undo" v-if="timerType !== 'now'" class="btn-icon fa-1x" @click="reset"/>
                        </div>
                    </div>
                    <div class="align-self-center" :style="{fontSize: style.fontSize * 2.2 + 'vw'}"
                         style="font-weight: lighter">
                        &nbsp;{{ getChar[1] }}
                    </div>
                </div>
                <div class="m-auto col-12 col-md-6 offset-md-3 col-lg-4 offset-lg-4 col-xl-2 offset-xl-5"
                     style="overflow-y: auto; height: 20vh; min-width: max-content">
                    <div class="list-group list-group-flush text-center">
                        <button type="button" v-for="(item, key) in timeLog"
                                class="list-group-item list-group-item-action"
                                :style="{fontSize: style.fontSize * 0.2 + 'vw', backgroundColor: style.bgColor, color: style.fontColor}">
                            {{ "#" + key + " - " + item }}
                        </button>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import screenfull from "screenfull";
    import leftPad from "left-pad";
    import * as workerTimers from "worker-timers";

    export default {
        metaInfo() {
            return {
                title: this.$i18n.t('title.timer'),
                meta: [],
            }
        },
        data() {
            return {
                timerType: "timer",
                supportStyleList: [
                    {color: "#000000", bgColor: "#FFFFFF"},
                    {color: "#FFFFFF", bgColor: "#000000"},
                    {color: "#2fff5a", bgColor: "#000000"},
                    {color: "#FFF700", bgColor: "#2980B9"},
                    {color: "#E19C23", bgColor: "#2368E1"},
                    {color: "#E12F23", bgColor: "#23D5E1"},
                    {color: "#8223E1", bgColor: "#82E123"},
                    {color: "#2EA064", bgColor: "#A02E6A"},
                    {color: "#a02427", bgColor: "#96a051"},
                ],
                separatorSymbol: [
                    {day: ":", hour: ":", min: ":", sec: "", microSec: "."},
                    {day: "D", hour: "H", min: "M", sec: "S ", microSec: ""},
                    {day: "天", hour: "時", min: "分", sec: "秒 ", microSec: ""},
                    {day: "Day", hour: "Hour", min: "Min", sec: "Sec ", microSec: ""}
                ],
                style: {
                    fontSize: 10,
                    bgColor: "#FFFFFF",
                    fontColor: "#000000",
                    charId: "{",
                    title: "",
                    subTitle: "",
                    separatorSymbolId: 0,
                },
                time: {
                    day: 0,
                    hour: 0,
                    min: 0,
                    sec: 0,
                    microsec: 0
                },
                countDown: {
                    day: 0,
                    hour: 0,
                    min: 0,
                    sec: 0
                },
                display: {
                    day: true,
                    hour: true,
                    min: true,
                    sec: true,
                    microSec: true
                },
                timeLog: [],
                startTime: 0,
                countDownTime: 0,
                pauseStartTime: 0,
                pauseTotalTime: 0,
                refreshTime: 10,
                requestId: null,
                isPause: true
            }
        },
        methods: {
            fullscreen() {
                screenfull.request($("#timerBody")[0]);
            },
            setColor(index) {
                this.style.fontColor = this.supportStyleList[index].color;
                this.style.bgColor = this.supportStyleList[index].bgColor;
            },
            play() {
                let now = Date.now();

                if (this.startTime === 0) {
                    this.startTime = now;
                    if (this.timerType === 'countDown') {
                        this.countDownTime = this.countDown.day * 86400000 + this.countDown.hour * 3600000 + this.countDown.min * 60000 + this.countDown.sec * 1000;
                    }
                }

                if (this.pauseStartTime) {
                    this.pauseTotalTime += now - this.pauseStartTime;
                    this.pauseStartTime = 0;
                }

                this.render();
                this.requestId = workerTimers.setInterval(this.render, this.refreshTime);
                this.isPause = false;
            },
            pause() {
                this.pauseStartTime = Date.now();
                workerTimers.clearInterval(this.requestId);
                this.requestId = null;
                this.isPause = true;
            },
            reset() {
                workerTimers.clearInterval(this.requestId);
                this.requestId = null;
                this.pauseStartTime = 0;
                this.pauseTotalTime = 0;
                this.startTime = 0;
                this.time.day = 0;
                this.time.hour = 0;
                this.time.min = 0;
                this.time.sec = 0;
                this.time.microsec = 0;
                this.isPause = true;
                this.timeLog = [];

                if (this.timerType === 'countDown') {
                    this.renderInitTime();
                }
            },
            timeup() {
                workerTimers.clearInterval(this.requestId);
                this.requestId = null;
                this.startTime = 0;
                this.time.microsec = 0;
                this.isPause = true;
            },
            renderInitTime() {
                this.time.day = this.countDown.day;
                this.time.hour = this.countDown.hour;
                this.time.min = this.countDown.min;
                this.time.sec = this.countDown.sec;
            },
            fetch() {
                this.timeLog.push(
                    this.time.day + ":" +
                    leftPad(this.time.hour, 2, 0) + ":" +
                    leftPad(this.time.min, 2, 0) + ":" +
                    leftPad(this.time.sec, 2, 0) + "." +
                    leftPad(Math.floor(this.time.microsec / 10), 2, 0)
                );
            },
            render() {
                let displayTime;
                let nowDate = (new Date());

                switch (this.timerType) {
                    case "timer":
                        displayTime = nowDate.getTime() - this.startTime - this.pauseTotalTime;
                        break;
                    case "countDown":
                        displayTime = this.countDownTime - (nowDate.getTime() - this.startTime) + this.pauseTotalTime;
                        if (displayTime <= 0) {
                            this.timeup();
                            return ;
                        }
                        break;
                    case "now":
                        this.time.hour = nowDate.getHours();
                        this.time.min = nowDate.getMinutes();
                        this.time.sec = nowDate.getSeconds();
                        this.time.microsec = nowDate.getTime() % 1000;
                        return;
                }

                if (displayTime >= 86400000) {
                    this.time.day = Math.floor(displayTime / 86400000);
                    displayTime -= this.time.day * 86400000;
                } else {
                    this.time.day = 0;
                }

                if (displayTime >= 3600000) {
                    this.time.hour = Math.floor(displayTime / 3600000);
                    displayTime -= this.time.hour * 3600000;
                } else {
                    this.time.hour = 0;
                }

                if (displayTime >= 60000) {
                    this.time.min = Math.floor(displayTime / 60000);
                    displayTime -= this.time.min * 60000;
                } else {
                    this.time.min = 0;
                }

                if (displayTime >= 1000) {
                    this.time.sec = Math.floor(displayTime / 1000);
                    displayTime -= this.time.sec * 1000;
                } else {
                    this.time.sec = 0;
                }

                this.time.microsec = displayTime;
            }
        },
        computed: {
            getChar() {
                switch (this.style.charId) {
                    case "0":
                        return ["", ""];
                    case "{":
                        return ["{", "}"];
                    case "{{":
                        return ["{{", "}}"];
                    case "[":
                        return ["[", "]"];
                    case "<":
                        return ["<", ">"];
                    case "/":
                        return ["/", "/"];
                    case "//":
                        return ["//", "//"];
                    case "\\/":
                        return ["\\", "/"];
                }
            },
            getHour() {
                return leftPad(this.time.hour, 2, 0);
            },
            getMin() {
                return leftPad(this.time.min, 2, 0);
            },
            getSec() {
                return leftPad(this.time.sec, 2, 0);
            },
            getMicroSec() {
                return leftPad(Math.floor(this.time.microsec / 10), 2, 0);
            }
        },
        watch: {
            timerType(value) {
                this.reset();

                if (value === 'now') {
                    this.play();
                }

            }
        },
        components: {},
        mounted() {

        }
    }
</script>

<style scoped lang="scss">

</style>